body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*background-color: rgb(20, 20, 20)*/
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.light-square {
    background-color: tan;
}

.dark-square {
    background-color: forestgreen;
}
.btn {
    background-color: rgb(156, 224, 202);
    font-size: 0.5em;
}
.win-announcement {
    background-color: lightgray;
    border-color: darkgray;
    border-width: 0.5em;
    border-style: solid;
    border-radius: 2em;
    width: 50vh;
    height: 25vh;
    top: 50%;
    margin-top: -17.5vh;
    left: 50%;
    margin-left: -25vh;
    position: absolute;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 2em;
    display: flex;
}

.show {
    z-index: 2;
}

.hide {
    visibility: hidden;
    z-index: -1;
}
.control-bar {
    background-color: lightgray;
    position: absolute;
    right: 0;
    height: 100vh;
    width: 20vw;
    text-align: center;
    overflow-y: auto;
}

.piece-settings {
    background-color: skyblue;
    width: 30vw;
    height: 30vh;
    -webkit-transform: translate(-45vw, 0);
            transform: translate(-45vw, 0);
    top: 30vh;
    position: absolute;
    overflow-y: auto;
    padding: 5px;

}

.setting-input {
    width: 2em;
    margin: 5px;
}

.setting-triplet {
    background-color: lightgreen;
    padding: 0.5em;
    display: flex;
    justify-content: space-around;
    border-color: rgb(20, 20, 20);
    border-style: solid;
    border-width: 2.5px;
    margin-top: 5px;
    margin-bottom: 5px;
    border-radius: 0.5em;
}

.delete-btn {
    color: red;
}

.delete-btn:hover {
    cursor: pointer;
}

.add-btn {
    color: green;
    font-size: 1.5em;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.add-btn:hover {
    cursor: pointer;
}

.cb-btn {
    margin: 5px;
    background-color: #455EB5;
    color: #fff;
    font-weight: 700;
    padding: 0.5em;
    border-radius: 0.5em;
    text-align: center;
    cursor: pointer;
}

.draw {
    background-color: steelblue;
    padding: 5px;
    margin: 5px;
    font-weight: 500;
    color: #FFF;
    text-align: center;
    border-radius: 0.5em;
}
